.btn-product {
    margin: 0 5px 10px 5px;
    background: #b1ad97;
    color: #555;
    font-size: 11px;
    line-height: 18px;
    border-radius: 25px;
    -webkit-border-radius: 25px;
    cursor: pointer;
    text-decoration: none;
    outline: none;
    box-shadow: 1px 1px 2px rgba(0,0,0,.1);
    text-transform: uppercase;
    font-weight: 700;
    position: relative;
    display: inline-block;
    vertical-align: bottom;
    text-align: center;
    font-family: 'Raleway', sans-serif;
}

.btn-product:before {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    border-radius: inherit;
    -webkit-border-radius: inherit;
    background: inherit;
    content: "";
    opacity: .3;
}

.btn-product span {
    display: block;
    position: relative;
}

.btn-product-icon {
    width: 30px;
    height: 30px;
    line-height: 30px;
    text-align: center;
    font-size: 20px;
    position: absolute!important;
    top: 50%;
    margin-top: -15px;
    margin-left: -15px;
    transition: all .15s;
    -webkit-transition: all .15s;
    left: -50px;
}

.btn-product-icon i {
    color: #000;
}

.btn-product-icon img {
    display: block;
    max-width: 30px;
    height: auto;
}

.btn-product-text {
    padding: 15px 74px;
    transition: all .3s .05s;
    -webkit-transition: all .3s .05s;
    color: #555!important;
}

.btn-product-text2 {
    padding: 15px 35px;
    font-size: 10px;
    font-family: 'Montserrat';
}

.btn-product:hover:before {
    transform: scale(1.5);
    -webkit-transform: scale(1.5);
    opacity: 0;
    visibility: hidden;
    transition: transform .5s, opacity .5s, visibility 0s .5s;
    -webkit-transition: transform .5s, opacity .5s, visibility 0s .5s;
}

.btn-product:hover .btn-product-icon {
    left: 50%!important;
}

.btn-product:hover .btn-product-text {
    left: 100%;
}











