.categories-structure {
    padding: 0;
    display: inline-block;
    cursor: pointer;
    transition-property: opacity, -webkit-filter;
    transition-property: opacity, filter;
    transition-property: opacity, filter, -webkit-filter;
    transition-duration: 0.15s;
    transition-timing-function: linear;
    font: inherit;
    color: inherit;
    text-transform: none;
    background-color: transparent;
    border: 0;
    margin: 0;
    overflow: visible;   
}

.categories-box {
    width: 26px;
    height: 26px;
    display: inline-block;
    position: relative;
}

.categories-inner, .categories-inner::before, .categories-inner::after {
    background-color: #111!important;
}

.categories-spin .categories-inner {
    transition-duration: 0.22s;
    transition-timing-function: cubic-bezier(0.55, 0.055, 0.675, 0.19);
}

.categories-inner, .categories-inner::before, .categories-inner::after {
    width: 26px;
    height: 2px;
    background-color: #fff;
    border-radius: 0;
    position: absolute;
    transition-property: transform;
    transition-duration: 0.15s;
    transition-timing-function: ease;
}

.categories-inner {
    display: block;
    top: 50%;
    margin-top: -3px;
}

.categories-spin .categories-inner::before {
    transition: top 0.1s 0.25s ease-in, opacity 0.1s ease-in;
}

.categories-inner::before {
    top: -9px;
}

.categories-inner::after {
    bottom: -9px;
}

.categories-inner::before, .categories-inner::after {
    content: "";
    display: block;
}

.categories-inner, .categories-inner::before, .categories-inner::after {
    width: 26px;
    height: 2px;
    background-color: #fff;
    border-radius: 0;
    position: absolute;
    transition-property: transform;
    transition-duration: 0.15s;
    transition-timing-function: ease;
}

