.accordion-checkbox {
    display: inline-block;
    cursor: pointer;
    margin: 0;
    padding: 0;
    min-width: 20px;
    min-height: 20px;
    --background: #fff;
    --border: #D1D6EE;
    --border-hover: #BBC1E1;
    --border-active: #1E2235;
    --tick: #fff;
    position: relative;
}

.accordion-checkbox input {
    display: none;
    margin: 4px 0 0;
    line-height: normal;
    box-sizing: border-box;
    padding: 0;
    -webkit-appearance: none;
    -moz-appearance: none;
    position: relative;
    outline: none;
    background: var(--background);
    border: none;
    cursor: pointer;
    border-radius: 4px;
    transition: box-shadow .3s;
    box-shadow: inset 0 0 0 1px var(--border);
    box-shadow: inset 0 0 0 var(--s, 1px) var(--b, var(--border))
}

.accordion-checkbox svg, .accordion-checkbox input {
    width: 21px;
    height: 21px;
    display: inline-block;
}

.accordion-checkbox svg {
    pointer-events: none;
    fill: none;
    stroke-width: 2px;
    stroke-linecap: round;
    stroke-linejoin: round;
    stroke: var(--border-active);
    stroke: var(--stroke, var(--border-active));
    position: absolute;
    top: 4px;
    left: 0;
    width: 21px;
    height: 21px;
    transform: scale(1) translateZ(0);
    transform: scale(var(--scale, 1)) translateZ(0);
}

.accordion-checkbox input:hover {
    --s: 2px;
    --b: var(--border-hover);
}

.accordion-checkbox.path input:checked {
    --s: 2px;
    transition-delay: 0.4s;
}

.accordion-checkbox.path input:checked + svg {
    --a: 16.1 86.12;
    --o: 102.22;
  }

.accordion-checkbox.path svg {
    stroke-dasharray: 86.12;
    stroke-dasharray: var(--a, 86.12);
    stroke-dashoffset: 86.12;
    stroke-dashoffset: var(--o, 86.12);
    transition: stroke-dasharray 0.6s, stroke-dashoffset 0.6s;
}

