    opacity: 1;
    transform: scale(1);
}

.input-effect {
    position: absolute;
    display: block;
    width: calc(100% + 2px);
    height: calc(100% + 2px);
    top: -1px;
    left: -1px;
    pointer-events: none;
    border: 1px solid #d6a5f7;
    border-radius: 3px;
    visibility: hidden;
    opacity: 0;
    transition: all 0.4s;
    transform: scaleX(1.1) scaleY(1.3);
}

.form-show-hide {
    font-size: 15px;
    color: #999999;
    display: flex;
    align-items: center;
    position: absolute;
    height: 100%;
    top: 0;
    right: 12px;
    padding: 0 5px;
    cursor: pointer;
    transition: background 0.4s;
}

.form-second-area {
    position: relative;
}

.form-remember {
    display: inline-block;
}

.form-second-area input {
    display: none;
    padding: 0;
    margin: 0;
    outline: none;
}

.remember-label {
    font-family: 'Raleway';
    font-size: 13px;
    color: #999999;
    line-height: 1.4;
    display: block;
    position: relative;
    padding-left: 26px;
    cursor: pointer;
}

.remember-label::before {
    content: "\f00c";
    font-family: FontAwesome;
    font-size: 13px;
    color: transparent;
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    width: 18px;
    height: 18px;
    border-radius: 2px;
    background: #fff;
    border: 1px solid #e6e6e6;
    left: 0;
    top: 50%;
    transform: translateY(-50%);
}

#formRemember:checked + .remember-label::before {
    color: #872e99;
}

.form-forget {
    display: inline-block;
    padding-left: 33px;
    margin: 0;
}

.form-forget a {
    font-family: 'Raleway';
    font-size: 13px;
    color: #555555;
    line-height: 1.4;

}

.form-button {
    width: 100%;
    display: flex;
    flex-wrap: wrap;
}


